<template>
  <v-navigation-drawer
    id="app-drawer"
    v-model="inputValue"
    app
    dark
    floating
    persistent
    mobile-break-point="991"
    width="260"
  >
    <v-img
      :src="image"
      :gradient="sidebarOverlayGradiant"
      height="100%"
    >
      <v-layout
        class="fill-height"
        tag="v-list"
        column
      >
        <v-list-tile avatar>
          <v-list-tile-avatar
            color="white"
          >
            <v-img
              :src="logo"
              height="34"
              contain
            />
          </v-list-tile-avatar>
          <v-list-tile-title class="title">
            Administartor
          </v-list-tile-title>
        </v-list-tile>
        <v-divider/>
        <v-list-tile
          v-if="responsive"
        >
          <v-text-field
            class="purple-input search-input"
            label="Search..."
            color="purple"
          />
        </v-list-tile>
        <v-list-tile
          v-for="(link, i) in links"
          :key="i"
          :to="link.to"
          :active-class="color"
          avatar
          class="v-list-item"
        >
          <v-list-tile-action>
            <v-icon>{{ link.icon }}</v-icon>
          </v-list-tile-action>
          <v-list-tile-title
            v-text="link.text"
          />
        </v-list-tile>
      </v-layout>
    </v-img>
  </v-navigation-drawer>
</template>

<script>
// Utilities
import {
  mapMutations,
  mapState
} from 'vuex'

export default {
  data: () => ({
    logo: './img/vuetifylogo.png',
    links: [
      {
        to: '/dashboard',
        icon: 'mdi-view-dashboard',
        text: '数据总览'
      },
      {
        to: '/user',
        icon: 'mdi-account',
        text: '用户管理'
      },
      {
        to: '/agent',
        icon: 'el-icon-s-shop',
        text: '城市代理'
      },
      {
        to: '/verify',
        icon: 'el-icon-s-check',
        text: '审核管理'
      },
      // {
      //   to: '/typography',
      //   icon: 'mdi-arrange-send-to-back',
      //   text: '发票管理'
      // },
      {
        to: '/coupon',
        icon: 'el-icon-s-ticket',
        text: '优惠券管理'
      },
      {
        to: '/vip',
        icon: 'mdi-account-box-multiple',
        text: '会员设置'
      },
      {
        to: '/order',
        icon: 'el-icon-s-order',
        text: '订单管理'
      },
      {
        to: '/anlysis',
        icon: 'el-icon-s-marketing',
        text: '数据报表'
      },
      {
        to: '/admin',
        icon: 'mdi-account-switch',
        text: '系统用户及权限'
      },
      {
        to: '/retail',
        icon: 'el-icon-collection-tag',
        text: '分销设置'
      },
      {
        to: '/text',
        icon: 'el-icon-reading',
        text: '文本信息'
      },
      {
        to: '/config',
        icon: 'el-icon-s-tools',
        text: '系统设置'
      },
      {
        to: '/wxappset',
        icon: 'el-icon-paperclip',
        text: '小程序设置'
      },
      {
        to: '/wxp',
        icon: 'el-icon-chat-round',
        text: '公众号设置'
      }
    ],
    responsive: false
  }),
  computed: {
    ...mapState('app', ['image', 'color']),
    inputValue: {
      get () {
        return this.$store.state.app.drawer
      },
      set (val) {
        this.setDrawer(val)
      }
    },
    items () {
      return this.$t('Layout.View.items')
    },
    sidebarOverlayGradiant () {
      return `${this.$store.state.app.sidebarBackgroundColor}, ${this.$store.state.app.sidebarBackgroundColor}`
    }
  },
  mounted () {
    this.onResponsiveInverted()
    window.addEventListener('resize', this.onResponsiveInverted)
  },
  beforeDestroy () {
    window.removeEventListener('resize', this.onResponsiveInverted)
  },
  methods: {
    ...mapMutations('app', ['setDrawer', 'toggleDrawer']),
    onResponsiveInverted () {
      if (window.innerWidth < 991) {
        this.responsive = true
      } else {
        this.responsive = false
      }
    }
  }
}
</script>

